<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">{{pageName}}仓库</h1>
          <label for="">仓库代码</label><el-input v-model="task.code" placeholder="请输入仓库代码"></el-input>
          <label for="">类型</label>
          <el-select v-model="task.type" placeholder="请选择">
            <el-option
              v-for="item in typeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
           
          <label for="">仓库名称</label><el-input v-model="task.name" placeholder="请输入仓库名称"></el-input>
          <label for="">所在国家</label><el-input v-model="task.country" placeholder="请输入所在国家"></el-input>
          <label for="">联系电话</label><el-input v-model="task.mobile" placeholder="请输入仓库联系电话"></el-input>
          <label for="">仓库地址</label><el-input v-model="task.address" placeholder="请输入仓库地址"></el-input>
           
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              code: "",
              name: "",
              country: "",
              mobile: "",
              address: '',
              type: ''
            },
           
            typeOptions: [
              {
                label: '自建仓',
                value: '自建仓'
              },
              {
                label: '合作仓',
                value: '合作仓'
              },
              {
                label: '海外仓',
                value: '海外仓'
              }
            ]
          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/warehouse";
          let params = {};
          params.code = this.task.code;
          params.name = this.task.name;
          params.country = this.task.country;
          params.mobile = this.task.mobile;
          params.address = this.task.address;
          params.type = this.task.type; 
          let that = this; 
          if(that.task.id){
            let config = {
              headers : { 
                'Content-Type':'application/x-www-form-urlencoded'
              },
            };
             url = "http://api.orkowms.me/warehouse/"+this.task.id ;
            var ret = this.$axios.put(url,params,config); 
          }else{
            var ret = this.$axios.post(url,params); 
          }

          ret.then((res) => {
            if(that.task.id){
              that.$message({
                type: 'success',
                message: '更新成功!'
              });
            }else {
              that.$message({
                type: 'success',
                message: '新增成功!'
              });
            } 
            that.task.code = '';
            that.task.name = '';
            that.task.country = '';
            that.task.mobile = '';
            that.task.address = '';
            that.task.type = '';
          }).catch((err) => {
            if(that.task.id){
              that.$message({
                type: 'error',
                message: '更新失败!'
              });
            }else {
              that.$message({
                type: 'error',
                message: '新增失败!'
              });
            }
          })
        },
         
      },
      created() {
        let row = this.$route.query;
        if(row.id){
          this.pageName = '修改';
          this.task.id = row.id;
        }else{
          this.task.id = null;
        }
        this.task.code = row.code;
        this.task.name = row.name;
        this.task.country = row.country;
        this.task.mobile = row.mobile;
        this.task.address = row.address;
        this.task.type = row.type;
         
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



